<template>
  <div class="row">
    <!-- Left Side -->
    <div class="col-md-6 my-auto">
      <div class="container">
        <transition name="fade" mode="out-in">
          <img
            v-if="changeSlide"
            key="slide-1"
            class="mySlides"
            src="../../static/images/slide-1.jpg"
            style="width: 100%;"
          >
          <img
            v-else
            key="slide-2"
            class="mySlides"
            src="../../static/images/slide-2.jpg"
            style="width: 100%;"
          >
        </transition>
      </div>
    </div>
    <!-- Right Side -->
    <div class="col-md-6 my-auto">
      <div class="container">
        <app-logo :homePage="true"></app-logo>
        <div class="w-75" style="margin: 0 auto; text-align: justified;">
          <p>
            Abby Tiska is a Pennsylvania-based communications professional with an affinity for words, research, and people. Abby has worked as a copywriter, recruiter and currently works as a content strategist, supporting a contract for a federal agency. Thanks for looking!
          </p>
          <app-social></app-social>
        </div>
      </div>
    </div>
  </div>
</template>


<script>

export default {
    data() {
        return {
            changeSlide: true
        };
    },
    created() {
        setInterval(() => {
            this.changeSlide = !this.changeSlide;
        }, 7000);
    }
}
</script>

<style>
.fade-enter {
    opacity: 0;
}

.fade-enter-active{
    transition: opacity 2.5s ease-out;
} 

.fade-leave-active {
    transition: opacity 2.5s ease-out;
    opacity: 0;
}

.fade-leave {
    
}
</style>



