        The following stories were written in a feature writing course I completed my senior year of college.
        The course strengthened my research, fact-checking, interviewing, transcribing and writing skills, and has helped me become a better journalist.
      </p>
      <hr class="ml-auto mr-auto">
    </div>

    <app-feature
      :imgLeft="true"
      imgSrc="../../../../static/images/feature_work/skincare.jpg"
      imgAlt="Skincare image, cleaning face with cotton pad"
      title="Three Skincare Ingredients to Consider for a Clearer Complexion"
      link="/static/Tiska_Skincare.pdf"
    >
      For this short service story, I got in contact with some estheticians to determine some of
      the best skin care ingredients to help fight acne. This piece stemmed from my growing interest
      in skin care and was a lot of fun to write as I learned many new things.
    </app-feature>
    <hr>
    <app-feature
      :imgLeft="false"
      imgSrc="../../../../static/images/feature_work/birdhouse.jpg"
      imgAlt="Birdhouse banner hanging in the venue"
      title="From House to Home: Inclusivity in DIY Music"
      link="/static/Tiska_Inclusivity_in_DIY_Music.pdf"
    >
      <template v-slot:caption>
        Photo by Jasmine Newland via
        <a
          class="link-p"
          href="https://www.facebook.com/birdhouseshippensburg/photos/a.201344883700292/333706223797490/?type=3&theater"
