      </template>This story was a personality profile on a Shippensburg University student, Ian Davies,
      who created a safer-space DIY venue in his apartment’s basement. Davies, a member of the LGBTQ+ community,
      books and hosts shows featuring diverse acts. I felt this was a really important piece to showcase the
      importance of diversity in music and the importance of safe settings at shows where everyone can freely be themselves.
    </app-feature>
