<template>
     <nav class="navbar navbar-expand-lg navbar-light navbar-custom">

      <button 
        class="navbar-toggler ml-auto"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
        @click="collapse = !collapse">
        <span class="navbar-toggler-icon"></span>
      </button>
    
      <div 
        class="navbar-collapse"
        :class="{ 'collapse-in': !collapse , 'collapse': collapse }"
        id="navbarSupportedContent"
        @click="collapse = !collapse">
        <ul class="navbar-nav ml-auto">
          <router-link to="/contact" class="nav-item" active-class="active" tag="li"><a class="nav-link">Contact</a></router-link>
          <router-link to="/about" class="nav-item" active-class="active" tag="li"><a class="nav-link">About</a></router-link>
          <router-link to="/portfolio" class="nav-item" active-class="active" tag="li"><a class="nav-link">Portfolio</a></router-link>
          <li class="nav-item">
            <a class="nav-link" href="/static/Abby_Tiska_Resume.pdf" target="_blank">Resume</a>
          </li>
        </ul>
      </div>
    </nav>
</template>

<script>
export default {
    data() {
        return {
            collapse: true
        }
    }
}
</script>



