import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home'
import About from '../components/About'
import Portfolio from '../components/portfolio/Portfolio'
import Copywriting from '../components/portfolio/Copywriting'
import PublishedWork from '../components/portfolio/PublishedWork'
import FeatureWork from '../components/portfolio/FeatureWork'
import Contact from '../components/Contact'
import NotFound from '../components/NotFound'

Vue.use(VueRouter)

export default new VueRouter({
    routes: [
        {
            path: '/',
            name: 'Home',
            component: Home,
        },
        {
            path: '/contact',
            name: 'contact',
            component: Contact,
        },
        {
            path: '/about',
            name: 'about',
            component: About,
        },
        {
            path: '/portfolio',
            name: 'portfolio',
            component: Portfolio,
        },
        {
            path: '/copywriting',
            name: 'copywriting',
            component: Copywriting,
        },
        {
            path: '/published_work',
            name: 'publishedWork',
            component: PublishedWork,
        },
        {
            path: '/feature_work',
            name: 'featureWork',
            component: FeatureWork,
        },
        {
            path: '*',
            name: '404',
            component: NotFound
        }
    ],
    mode: 'history'
})



