import Vue from 'vue'
import Vuex from 'vuex'

const fb = require('../firebaseConfig')

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        publishedWorks: [],
        firebase: fb
    },
    actions: {
        setPublishedWorks: ({ commit }, publishedWorks) => {
            commit('setPublishedWorks', publishedWorks);
        },
        fetchPublishedWorks: ({ commit }) => {
            fb.publishedWorkCollection.get()
                .then(querySnapshot => {
                    let works = [];
                    querySnapshot.forEach(doc => {
                        works.push(doc.data());
                    });
                    commit('setPublishedWorks', works);
                }).catch(err => {
                    console.log(err);
                });
        }
    },
    mutations: {
        setPublishedWorks: (state, publishedWorks) => {
            state.publishedWorks = [...publishedWorks];
        }
    },
    getters: {
        publishedWorks: state => {
            return state.publishedWorks;
        },
        firebase: state => {
            return state.firebase;
        }
    }
});


