<template>
    <div class="col-md-4 mx-auto card article-card">
      <hr class="d-sm-block d-md-none">
      <figure class="figure mx-auto d-block">
        <img class="figure-img img-fluid rounded shadow mx-auto d-block" :src="imgSrc">
        <figcaption class="figure-caption">{{ article.caption }}</figcaption>
      </figure>
      <div class="card-body">
        <h3><a :href="article.link">{{ article.title }}</a></h3>
        <p class="card-text">{{ article.summary }}</p>
      </div>
      <hr class="d-sm-block d-md-none">
    </div>
</template>

<script>
export default {
  props: ['article'],
  methods: {
    staticSource() {
      const basePath = '/static/images/published_work/';
      return basePath + this.article.image;
    }
  },
  computed: {
    imgSrc() {
        return this.staticSource();
    }
  },
  created() {
      //  this.setSource();

      // var storageRef = this.$store.state.fb.storageRef;
      // storageRef.child(this.article.image).getDownloadURL()
      //     .then(url => {
      //         this.article.image = url;
      //     })
      //     .catch(err => {
      //         console.log(err);
      //     });
  }
}
</script>

<style>
.article-card {
    margin: 10px 30px 10px 30px;
    
}
</style>



