<template>
    <div class="row">
        <div v-if="imgLeft" class="col-md-4">
            <figure class="figure mx-auto d-block">
                <img class="img-fluid rounded mx-auto d-block" :src="imgSrc" :alt="imgAlt">
                <figcaption class="figure-caption mx-auto d-block"><slot name="caption"></slot></figcaption>
            </figure>
        </div>
        <div class="col-md-8 order-md-1 order-2">
            <h2><a :href="link" target="_blank">{{ title }}</a></h2>
            <p>
                <slot></slot>
            </p>
        </div>
        <div v-if="!imgLeft" class="order-sm-1 order-md-2 col-md-4">
            <figure class="figure mx-auto d-block">
                <img class="img-fluid rounded mx-auto d-block" :src="imgSrc" :alt="imgAlt">
                <figcaption class="figure-caption mx-auto d-block"><slot name="caption"></slot></figcaption>
            </figure>
        </div>
    </div>
</template>

<script>
export default {
    props: [
        'imgLeft',
        'imgSrc',
        'imgAlt',
        'caption',
        'title',
        'link'
    ]
}
</script>



