<template>
  <div class="container">
    <app-logo></app-logo>

    <div class="container text-center">
      <h1 style="font-family: 'Chivo';">Published Work</h1>
      <p>
        Since 2017, I worked as a staff writer for my university newspaper,
        <a class="link-p" href="https://www.theslateonline.com/">The Slate</a>, where I was eventually promoted to assistant news editor.
        Through my opportunities and experience at The Slate, I was able to communicate and efficiently work alongside my peers in a professional
        environment while developing my skills to become a better journalist. Below are some stories I covered during my time with The Slate.
      </p>
      <hr>
    </div>

      <div class="row">
        <app-article v-for="article in publishedWorks" :key="article.id" :article="article"></app-article>
      </div>
    <app-social></app-social>
  </div>
</template>


<script>
import Article from './Article'
import { mapActions } from 'vuex'
import { mapGetters } from 'vuex'

export default {
    computed: {
        ...mapGetters(['publishedWorks'])
    },
    methods: {
        getWorks() {
            this.works = [this.$store.getters.getPublishedWorks];
        }
    },
    created() {
        this.$store.dispatch('fetchPublishedWorks');
    },
    components: {
        appArticle: Article
    }
}
</script>



