<template>
  <div class="container">
    <app-logo></app-logo>

    <div class="container text-center" style="padding: 10px;">
      <h1 style="font-family: 'Poppins', sans-seriff; padding: 10px; letter-spacing: 3px;">Let's talk!</h1>
    </div>

    <form id="contact-form" action="https://formspree.io/f/xpzolyld" method="POST">
      <div class="messages"></div>
      <div class="controls">
        <div class="row">
          <div class="col-sm-12 col-md-6">
            <div class="form-group">
              <label for="first_name">First name *</label>
              <input
                id="first_name"
                type="text"
                name="name"
                class="form-control"
                placeholder="Please enter your first name"
                required="required"
                data-error="Firstname is required."
                v-model="firstName"
              >
              <div class="help-block with-errors"></div>
            </div>
          </div>
          <div class="col-sm-12 col-md-6">
            <div class="form-group">
              <label for="last_name">Last name *</label>
              <input
                id="last_name"
                type="text"
                name="surname"
                class="form-control"
                placeholder="Please enter your last name"
                required="required"
                data-error="Lastname is required."
                v-model="lastName"
              >
              <div class="help-block with-errors"></div>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <div class="form-group">
              <label for="email">Email *</label>
              <input
                id="email"
                type="email"
                name="email"
                class="form-control"
                placeholder="Please enter your email"
                required="required"
                data-error="Valid email is required."
                v-model="email"
              >
              <div class="help-block with-errors"></div>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <div class="form-group">
              <label for="message">Message *</label>
              <textarea
                id="message"
                name="message"
                class="form-control"
                placeholder="Message for me"
                rows="4"
                required="required"
                data-error="Please, leave us a message."
                v-model="message"
              ></textarea>
              <div class="help-block with-errors"></div>
            </div>
          </div>
          <div class="col-md-12">
            <input
              type="submit"
              class="btn"
              :class="{'btn-submit': !disableSubmit, 'btn-secondary': disableSubmit }"
              :disabled="disableSubmit"
              value="Send"
              
            >
          </div>
        </div>
      </div>
    </form>

    <app-social></app-social>

    <app-modal v-if="showModal" @close="closeModal()">
      <h3 slot="header">Submission Details</h3>
      <p v-if="errors.length < 1" slot="body">{{ modalMessage }}</p>
      <ul v-else slot="body">
        <li v-for="(error, index) in errors" :key="index" class="li-error">{{ error }}</li>
      </ul>
    </app-modal>
  </div>
</template>

<script>
import Modal from './shared/Modal'

export default {
    data() {
        return {
            firstName: '',
            lastName: '',
            email: '',
            message: '',
            modalMessage: '',
            errors: [],
            showModal: false
        };
    },
    computed: {
        disableSubmit() {
            return this.firstName.length == 0 || this.lastName.length == 0 || this.email.length == 0 || this.message.length == 0;
        }
    },
    methods: {
        closeModal() {
            this.errors = [];
            this.showModal = false;
        },
        submitForm() {
            if(this.validate()){

                var formData = {
                    'contact-submit'        : '1',
                    'first_name'    : this.firstName,
                    'last_name'     : this.lastName,
                    'email'         : this.email,
                    'message'       : this.message
                }

                console.log('Submitted');

                $.ajax({
                    type    : 'POST',
                    url     : '../../../static/php/send_mail.php',
                    data    : formData,
                    success : response => {

                        if(response == '1'){
                            
                            this.modalMessage = 'Message sent! Thanks!';
                            this.showModal = true;
                            console.log("Message sent successfully.");

                        } else {

                            this.modalMessage = 'Uh oh!' + response;
                            this.showModal = true;
                            console.log("Failed to send the message.");
                        }
                    }
                });
            }
        },
        validate(){

            if(!this.validateFirstName() || !this.validateLastName()) {
                this.errors.push('Restricted characters in first or last name!');
            }

            if(!this.validateEmail()){
                this.errors.push('Invalid email entered!');
            }

            if(this.errors.length > 0) {
                this.showModal = true;
                return false;
            }
            return true;
        },
        validateEmail() {
            var re = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
            return re.test(this.email);
        },
        validateName(name) {
            var re = /^[a-zA-Z]+$/;
            return re.test(name);
        },
        validateFirstName() {
            return this.validateName(this.firstName);
        },
        validateLastName() {
            return this.validateName(this.lastName);
        }
    },
    components: {
        appModal: Modal
    }
}
        
</script>

<style>
.li-error {
    color: salmon;
    list-style: none;
}
</style>



